:root{
  --primary: white;
  --secondary: rgb(80, 31, 228);
  --dark: #888;
  --light: #eee
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Orbitron', sans-serif;
}

body {
  background-color: var(--light);
}

.App{
display:flex;
align-items: center;
justify-content: center;
min-height: 100vh;
}

.calculator{
width: 100vh;
max-width: 27rem;
background-color: var(--primary);
overflow: hidden;
border-radius: 2rem;
box-shadow: 0px 1rem 1rem rgba(136, 136, 136, 0.5);
}

.display{
  padding: 2rem;
  text-align: right;
  font-weight: 700;
  font-size: 1.7rem;
  background-color: var(--primary);
}

.display span{
  font-size: 1rem;
  color: #888;
}

button{
  appearance: none;
  border: none;
  outline: none;
  font-size: 1.7rem;
  padding: 2rem;
  cursor: pointer;
  transition: 0.4s;
}

button:hover{
  opacity: 0.7;
}

.oprators{
  display: flex;
}

.oprators button {
  flex : 1 1 0%;
  font-size: 1.5rem;
  font-weight: 400;
  background-color: var(--secondary);
  color: var(--primary);
}


.digits{
  display: flex;
  flex-wrap: wrap;
}

.digits button{
  background-color: var(--primary);
  flex: 1 1 33.33%;
  width: 33.33%;
  font-size: 1.5rem;
  font-weight: 400;
  transition: 0.4s;
}